import { log } from './Log';
import { EVT } from './EVT';
import { Config } from './Config';
import { settings } from './setting/Settings';
import { msgBox } from './MsgBox';
import { lang } from './Lang';
// 审查每个文件的数据，决定是否要下载它
class Filter {
    constructor() {
        this.bindEvents();
    }
    // 对启用了的过滤选项显示提示
    showTip() {
        this.getFeeType();
        this.getFeeRange();
        this.getIdRange();
        this.getPostDate();
        this.getTitleMustText();
        this.getTitleCannotText();
        this.getFileNameIncludes();
        this.getFileNameExcludes();
    }
    getFeeType() {
        if (settings.free && settings.pay) {
            return;
        }
        let msg = '';
        if (settings.free) {
            msg = `${lang.transl('_费用类型')}: ${lang.transl('_免费投稿')}`;
        }
        if (settings.pay) {
            msg = `${lang.transl('_费用类型')}: ${lang.transl('_付费投稿')}`;
        }
        log.warning(msg);
    }
    getFeeRange() {
        if (!settings.feeSwitch) {
            return;
        }
        const msg = `${lang.transl('_价格范围')}: ${lang.transl('_最小值')} ${settings.fee}¥`;
        log.warning(msg);
    }
    // 提示 id 范围设置
    getIdRange() {
        if (!settings.idRangeSwitch) {
            return;
        }
        log.warning(`id ${settings.idRange} ${settings.idRangeInput}`);
    }
    // 提示投稿时间设置
    getPostDate() {
        if (!settings.postDate) {
            return;
        }
        if (isNaN(settings.postDateStart) || isNaN(settings.postDateStart)) {
            const msg = lang.transl('_日期时间格式错误');
            this.showWarning(msg);
        }
        else {
            const start = new Date(settings.postDateStart).toLocaleString();
            const end = new Date(settings.postDateEnd).toLocaleString();
            log.warning(`${lang.transl('_时间范围')}: ${start} - ${end}`);
        }
    }
    getTitleMustText() {
        if (!settings.titleMustTextSwitch) {
            return;
        }
        const msg = `${lang.transl('_投稿标题必须含有文字')}: ${settings.titleMustText.toString()}`;
        log.warning(msg);
    }
    getTitleCannotText() {
        if (!settings.titleCannotTextSwitch) {
            return;
        }
        const msg = `${lang.transl('_投稿标题不能含有文字')}: ${settings.titleCannotText.toString()}`;
        log.warning(msg);
    }
    getFileNameIncludes() {
        if (!settings.fileNameIncludeSwitch ||
            settings.fileNameInclude.length === 0) {
            return;
        }
        const msg = `${lang.transl('_文件名中必须含有文字')}: ${settings.fileNameInclude.toString()}`;
        log.warning(msg);
    }
    getFileNameExcludes() {
        if (!settings.fileNameExcludeSwitch ||
            settings.fileNameExclude.length === 0) {
            return;
        }
        const msg = `${lang.transl('_文件名中不能含有文字')}: ${settings.fileNameExclude.toString()}`;
        log.warning(msg);
    }
    /**生成文章的 URL */
    createPostURL(option) {
        if (option.creatorId && option.id) {
            return `<a href="https://www.fanbox.cc/@${option.creatorId}/posts/${option.id}" target="_blank">${option.title}</a>`;
        }
        return option.title;
    }
    // 检查投稿是否符合过滤器的要求
    // 想要检查哪些数据就传递哪些数据，不需要传递 FilterOption 的所有选项
    check(option) {
        if (!this.checkFileType(option.ext)) {
            return false;
        }
        if (!this.checkfeeType(option.fee)) {
            log.warning(lang.transl('_跳过文章因为', this.createPostURL(option)) +
                lang.transl('_费用类型'));
            return false;
        }
        if (!this.checkfeeRange(option.fee)) {
            log.warning(lang.transl('_跳过文章因为', this.createPostURL(option)) +
                lang.transl('_价格范围'));
            return false;
        }
        if (!this.checkIdRange(option.id)) {
            log.warning(lang.transl('_跳过文章因为', this.createPostURL(option)) +
                lang.transl('_id范围'));
            return false;
        }
        if (!this.checkPostDate(option.date)) {
            log.warning(lang.transl('_跳过文章因为', this.createPostURL(option)) +
                lang.transl('_投稿时间'));
            return false;
        }
        if (!this.checkTitltMustText(option.title)) {
            log.warning(lang.transl('_跳过文章因为', this.createPostURL(option)) +
                lang.transl('_投稿标题必须含有文字'));
            return false;
        }
        if (!this.checkTitltCannotText(option.title)) {
            log.warning(lang.transl('_跳过文章因为', this.createPostURL(option)) +
                lang.transl('_投稿标题不能含有文字'));
            return false;
        }
        if (!this.checkFileNameInclude(option.name)) {
            log.warning(lang.transl('_跳过文章因为', `${option.name}.${option.ext}`) +
                lang.transl('_文件名中必须含有文字') +
                ': ' +
                settings.fileNameInclude.join(','));
            return false;
        }
        if (!this.checkFileNameExclude(option.name)) {
            log.warning(lang.transl('_跳过文章因为', `${option.name}.${option.ext}`) +
                lang.transl('_文件名中不能含有文字') +
                ': ' +
                settings.fileNameExclude.join(','));
            return false;
        }
        return true;
    }
    checkFileType(ext) {
        // 如果没有传递 ext，则保留这个文件
        if (!ext) {
            return true;
        }
        // 检查 ext 存在于哪种类型里，然后检查这个类型是否被选中
        for (const [key, value] of Object.entries(Config.fileType)) {
            if (value.includes(ext)) {
                return settings[key];
            }
        }
        // 如果这个 ext 不存在任何规定的类型里，则把它当作 other 类型，决定是否保留
        return settings.other;
    }
    checkfeeType(fee) {
        if (fee === undefined) {
            return true;
        }
        if (fee > 0) {
            return settings.pay;
        }
        else {
            return settings.free;
        }
    }
    checkfeeRange(fee) {
        if (fee === undefined || !settings.feeSwitch) {
            return true;
        }
        if (settings.feeRange === '>=') {
            return fee >= settings.fee;
        }
        else {
            return fee === settings.fee;
        }
    }
    checkIdRange(id) {
        if (id === undefined || !settings.idRangeSwitch) {
            return true;
        }
        const nowId = parseInt(id.toString());
        const setId = settings.idRangeInput;
        if (settings.idRange === '>') {
            return nowId > setId;
        }
        else {
            return nowId < setId;
        }
    }
    checkPostDate(date) {
        if (!settings.postDate ||
            date === undefined ||
            !settings.postDateStart ||
            !settings.postDateEnd) {
            return true;
        }
        const nowDate = new Date(date);
        return (nowDate.getTime() >= settings.postDateStart &&
            nowDate.getTime() <= settings.postDateEnd);
    }
    checkTitltMustText(title) {
        if (!settings.titleMustTextSwitch ||
            !title ||
            settings.titleMustText.length === 0) {
            return true;
        }
        title = title.toLowerCase();
        const match = settings.titleMustText.filter((str) => title.includes(str.toLowerCase()));
        if (match.length === 0) {
            return false;
        }
        return true;
    }
    checkTitltCannotText(title) {
        if (!settings.titleCannotTextSwitch ||
            !title ||
            settings.titleCannotText.length === 0) {
            return true;
        }
        title = title.toLowerCase();
        const match = settings.titleCannotText.filter((str) => title.includes(str.toLowerCase()));
        if (match.length > 0) {
            return false;
        }
        return true;
    }
    checkFileNameInclude(name) {
        if (!settings.fileNameIncludeSwitch ||
            settings.fileNameInclude.length === 0 ||
            !name) {
            return true;
        }
        const find = settings.fileNameInclude.some((str) => name.toLowerCase().includes(str.toLowerCase()));
        return find;
    }
    checkFileNameExclude(name) {
        if (!settings.fileNameExcludeSwitch ||
            settings.fileNameExclude.length === 0 ||
            !name) {
            return true;
        }
        const find = settings.fileNameExclude.some((str) => name.toLowerCase().includes(str.toLowerCase()));
        return !find;
    }
    // 如果设置项的值不合法，则显示提示
    showWarning(msg) {
        EVT.fire('wrongSetting');
        msgBox.error(msg);
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.showTip();
        });
    }
}
const filter = new Filter();
export { filter };
