import { EVT } from './EVT';
// 存储抓取结果和状态
class Store {
    constructor() {
        this.postIdList = [];
        /**抓取结果的元数据 */
        this.resultMeta = [];
        /**抓取结果 */
        this.result = [];
        /**抓取完成的时间 */
        this.date = new Date();
        /**因为价格限制而不能抓取的文章 */
        this.skipDueToFee = 0;
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.resetResult();
        });
    }
    getCommonData(data) {
        return {
            postId: data.postId,
            type: data.type,
            title: data.title,
            date: data.date,
            fee: data.fee,
            user: data.user,
            uid: data.uid,
            createID: data.createID,
            tags: data.tags,
        };
    }
    // 添加每个作品的信息。只需要传递有值的属性
    addResult(data) {
        this.resultMeta.push(data);
        // 为投稿里的所有的 文本内容 生成一份数据
        // 但是此时并不会生成文本的 URL，等到下载时才会为其生成 URL
        if (data.textContent.text.length > 0) {
            const result = Object.assign(this.getCommonData(data), data.textContent);
            this.result.push(result);
        }
        // 为投稿里的每个 files 生成一份数据
        const files = data.files;
        for (const fileData of files) {
            const result = Object.assign(this.getCommonData(data), fileData);
            this.result.push(result);
        }
    }
    resetResult() {
        this.postIdList = [];
        this.resultMeta = [];
        this.result = [];
        this.skipDueToFee = 0;
    }
}
const store = new Store();
export { store };
